.spinner{
  width: 70px;
  height: 70px;
  background-image: url("../../assets/images/pokeball.png");
  background-size: cover;
  display: inline-block;
  animation: slidein 0.9s infinite ease-in-out;
  align-self: start;
}
@keyframes slidein {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}