.types-container{
  margin-top: 5px;
  display: flex;
  margin-bottom: 10px;
  flex-wrap: wrap;
  user-select:none;
}
.type-selector{
  flex: 0 0 auto;
  margin-right: 10px;
  border:1px solid var(--main-yellow-color);
  padding:2px 10px;
  border-radius: 15px;
  margin-bottom: 5px;
  padding-right:35px;
  cursor: pointer;
}
.type-selector:hover,
.type-selector.active{
  background:var(--hover-dark-color);
}