.details-wrapper{
  display: flex;
  justify-content: center;
  width: 50%;
}
@media screen and (max-width: 570px) {
  .details-wrapper{
    max-width: 100%;
    width: auto;
    margin-bottom: 10px;
  }
}
.pokemon-details{
  display:flex;
  flex-direction: column;
  align-items: center;
}
.btn{
  margin-top: 10px;
  background:none;
  border:1px solid rgb(253, 216, 94);
  border-radius: 15px;
  cursor: pointer;
  padding:5px;
  transition: background .1s linear, color .1s linear;
  max-width: 207px;
}
.btn:hover{
  background:rgb(253, 216, 94);
  color:var(--body-color);
}
.remove-btn:hover{
  background:rgb(250, 102, 57);
}
