:root{
  --main-yellow-color:rgb(254,202,27);
  --body-color:#263238;
  --hover-dark-color:#171e22;
  /* types */
  --normal-color:navajowhite;
  --fighting-color:floralwhite;
  --flying-color:#FFFFFF;
  --poison-color:#93e51e;
  --ground-color:rgb(80, 57, 22);
  --rock-color:grey;
  --bug-color:springgreen;
  --ghost-color:grey;
  --steel-color:steelblue;
  --fire-color:rgb(255, 153, 0);
  --water-color:rgb(49, 27, 240);
  --grass-color:green;
  --electric-color:rgb(201, 164, 43);
  --psychic-color:powderblue;
  --ice-color:rgb(169, 169, 248);
  --dragor-color:darkolivegreen;
  --dark-color:rgb(54, 3, 90);
  --fairy-color:rgb(240, 53, 199);
  --shadow-color:rgb(53, 56, 59);
  /* types */

}
*{
  padding:0;
  margin:0;
  list-style:none;
  color:var(--main-yellow-color);
  font-size:25px;
  outline:none;
}
body{
  background:var(--body-color);
}
.ct-toast{
  padding:10px 20px;
  background:var(--body-color);
  border:none !important;
}
.ct-toast-success{
  box-shadow: 0px 0px 5px 1px rgb(110, 192, 95);
}
.ct-toast-error{
  box-shadow: 0px 0px 5px 1px rgb(214, 10, 46);
}
.ct-text{
  font-size: 20px;
}

/* TYPES */

.type-icon{
  position: relative;
}
.type-icon::after{
  position: absolute;
  font-family: "Font Awesome 5 Free";
  font-size: 18px;
  top:50%;
  transform: translateX(5px) translateY(-50%);
  font-weight: 900;
}

.normal{
  color:var(--normal-color);
}
.icon-normal::after{
  content:'\f1b0';
}
.border-normal{
  box-shadow: 0px 0px 20px -7px var(--normal-color);
}

.fighting{
  color:var(--fighting-color);
}
.icon-fighting::after{
  content:'\f255';
}
.border-fighting{
  box-shadow: 0px 0px 20px -7px var(--fighting-color);
}

