.input-wrapper{
  width: 100%;
  position: relative;
  margin-bottom: 10px;
}
.search-input{
  width: 100%;
  box-sizing: border-box;
  border:1px solid var(--main-yellow-color);
  background:none;
  padding:7px;
  border-radius: 15px;
}
.clear-input-icon{
  cursor: pointer;
  position: absolute;
  font-size: 20px;
  top:50%;
  transform: translateX(-200%) translateY(-50%);
}

.custom-placeholder{
  position: absolute;
  padding:0px 5px;
  top:50%;
  left:10px;
  transform: translateY(-55%);
  transition: transform 0.2s linear, color 0.2s linear;
  background:var(--body-color);
  color:rgba(236, 209, 119, 0.5);
  border-radius: 100px;
  pointer-events: none;
}
.search-input:focus~.custom-placeholder,
.search-input:valid~.custom-placeholder{
  transform: translateY(-150%) translateX(-5px);
  color:rgba(254,202,27, 1)
}
@media screen and (max-width: 570px) {
  .input-wrapper{
    margin-top:20px;
  }
}