@font-face {
  font-family: Pokemon;
  src: url("../../assets/fonts/Pokemon\ Solid.ttf");
}
header{
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  position: relative;
}
@media screen and (max-width: 570px) {
  header{
    align-items: flex-start;
  }
  header h1{
    font-size: 40px;
  }  
}

h1 {
  font-size: 72px;
  font-family: Pokemon;
  line-height: 150%;
  -webkit-text-stroke: 2px steelblue;
}
.nav-list{
  display: flex;
}
.nav-item{
  margin-right:30px;
}
.nav-item:last-child{
  margin-right:0px;
}
@media screen and (max-width: 665px) {
  .nav-list{
    top: 0;
    left:100%;
    position: fixed;
    width: 100vw;
    height: 100vh;
    flex-direction: column;
    align-items: center;
    z-index: 1;
    background:var(--body-color);
    transition: transform 0.5s ease-in-out;
    transform:translateX(0%);
  }
  .nav-list.opened{
    transform:translateX(-100%);
    
  }
  .nav-item{
    margin-right: 0px;
    margin-top: 50px;
  }
}
header a{
  text-decoration: none;
  font-family: Pokemon;
  -webkit-text-stroke: 2px steelblue;
  font-size: 30px;
}

.menu-btn{
  position: absolute;
  top:0;
  right:0;
  display:none;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 50px;
  height: 50px;
  cursor: pointer;
  z-index: 2;
}
.btn-burger,
.btn-burger::after,
.btn-burger::before{
  width:40px;
  height: 5px;
  background:var(--main-yellow-color);
  border-radius: 5px;
  transition: all .5s ease-in-out;
}
.btn-burger::after,
.btn-burger::before{
  position: absolute;
  content: "";
}
.btn-burger::after{
  transform: translateY(-15px);
}
.btn-burger::before{
  transform: translateY(15px);
}
.menu-btn.opened .btn-burger{
  transform: translateX(50px);
  background:transparent;
}
.menu-btn.opened .btn-burger::after{
  transform: translateX(-50px) rotate(45deg);
}
.menu-btn.opened .btn-burger::before{
  transform: translateX(-50px) rotate(-45deg) ;
}
@media screen and (max-width: 665px) {
  .menu-btn{
    display: flex;
  }
}
