.list-wrapper{
  width: 50%;
}
@media screen and (max-width: 570px) {
  .list-wrapper{
    max-width: 100%;
    width: auto;
  }
}
.pokemon-list{
  list-style: none;
  text-align: center;
  border:1px solid var(--main-yellow-color);
  border-radius: 15px;
}
.pagination{
  display:flex;
  justify-content: center;
  margin-top: 10px;
  user-select:none;
}
.pagination li{
  border:1px solid var(--main-yellow-color);
  text-align: center;
  transition: background .2s linear;
  cursor: pointer;
  border-radius: 10px;
}
.pagination li:hover{
  background:var(--hover-dark-color);
}
.pagination li a{
  width: 45px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination li:first-child{
  margin-right: auto;
}
.pagination li:last-child{
  margin-left: auto;
}
.pagination .active{
  margin:0px 10px;
  background:var(--hover-dark-color);
}
.pagination .break-me{
  border:none;
}
@media screen and (max-width: 720px) {
  .pagination{
    max-width: 100%;
  }
  .pagination .active{
    margin: 0px;
  }
  .pagination li a{
    font-size: 15px;
    width: 35px;
    height: 35px;
  }
}