.pokemon-info{
  border:1px solid var(--main-yellow-color);
  border-radius: 15px;
  padding:10px;
  display:flex;
  flex-direction: column;
  position: relative;
}
.name{
  align-self: center;
  margin-bottom: 10px;
  font-size: 35px;
}
.name::after{
  display:none;
}
.pokemon-info-item{
  padding:5px;
  border-bottom:1px solid var(--main-yellow-color);
  text-align: start;
}
.sprites img{
  width:50%;
  height: auto;
}
.close-icon{
  font-size: 20px;
  position: absolute;
  top:0;
  right:0;
  transform: translateX(-80%) translateY(20%);
  cursor:pointer;
}
.second-level{
  border:none;
}
.types{
  cursor: pointer;
  position: relative;
  display: inline-block;
  width: 100%;
}
.types::after,
.types::before{
  position: absolute;
  content:"";
  width: 15px;
  height: 2px;
  background:var(--main-yellow-color);
  top:13px;
  right:5px;
  transition: transform .2s linear;
}
.types::after{
  right:8px;
  transform: translateX(-25%) rotate(-45deg);
}
.types::before{
  transform: translateX(25%) rotate(45deg);
}
.types.active::after{
  transform: translateX(-25%) rotate(45deg);
}
.types.active::before{
  transform: translateX(25%) rotate(-45deg);
}
.pokemon-info .btn.btn-stats{
  align-self: center;
}

