.pokemon-collection-wrapper{
  display: flex;
  flex-direction: column;
}
.pokemon-collection-wrapper .btn{
  margin-bottom: 10px;
  align-self: flex-end;
}
.pokemon-collection{
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}
.pokemon-collection .pokemon-info{
  margin-right: 1%;
  margin-bottom: 10px;
  width:24%;
  box-sizing: border-box;
}
.pokemon-collection .pokemon-info:nth-child(4n+4){
  margin-right: 0px;
}
@media screen and (max-width: 830px) {
  .pokemon-collection{
    justify-content: space-between;
  }
  .pokemon-collection .pokemon-info{
    margin-right: 0px;
    width: 49%;
  }
}
@media screen and (max-width: 425px) {
  .pokemon-collection{
    justify-content: flex-start;
  }
  .pokemon-collection .pokemon-info{
    margin-right: 0px;
    width: 100%;
  }
}


