.modal{
  position: fixed;
  top:50%;
  left:50%;
  transform: translateX(-50%) translateY(-50%);
  background:var(--body-color);
  padding:20px;
  border:1px solid var(--main-yellow-color);
  border-radius: 10px;
  z-index: 5;
}
@media screen and (max-width: 570px) {
  .modal{
    width: 95%;
    box-sizing: border-box;
  }
}
.modal h2{
  text-align: center;
}
.stats-list{
  margin-top: 10px;
}
.stats-item{
  border-bottom: 1px solid var(--main-yellow-color);
  padding:5px;
}
