.answer-item{
  box-sizing: border-box;
  width:19%;
  padding:10px;
  border:1px solid var(--main-yellow-color);
  border-radius: 10px;
  cursor: pointer;
  margin-right: 1%;
  margin-bottom: 10px;
}
@media screen and (max-width: 570px) {
  .answer-item{
    width:49%;
  }
}
.answer-cart{
  display:flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}